@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap);
*{font-family:"Ubuntu", sans-serif}*,*::before,*::after{margin:0;padding:0;border:0;font:inherit;vertical-align:baseline;box-sizing:border-box;font-family:"Ubuntu", sans-serif;color:#1e272e}html{scroll-behavior:smooth}body{overflow-x:hidden}a{text-decoration:none;color:#1e272e}li{list-style:none}input:focus{outline:none}#app{width:100vw;min-height:100vh;padding:1rem 5%;background-color:#f4f4f4;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}#app>h1{text-align:center;font-size:5rem;font-weight:bold}input{width:250px;height:3rem;padding:0 1rem;margin:1rem 0 2rem;font-size:1.8rem;color:grey;background:white;box-shadow:0px 4px 4px rgba(0,0,0,0.1);border-radius:6px}input::-webkit-input-placeholder{color:grey}input:-ms-input-placeholder{color:grey}input::placeholder{color:grey}.userWrapper{width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-wrap:wrap;flex-wrap:wrap}.userWrapper>h1{margin-top:1rem;font-size:2rem}.userCard{width:300px;height:380px;margin:1rem;cursor:pointer;background:white;box-shadow:0px 4px 4px rgba(0,0,0,0.1);border-radius:6px;transition:0.3s}.userCard:hover,.userCard:focus{box-shadow:0 0.5em 0.5em -0.4em rgba(0,0,0,0.3);-webkit-transform:translateY(-0.25em);transform:translateY(-0.25em)}.userCard>img{border-top-left-radius:6px;border-top-right-radius:6px;-webkit-filter:brightness(80%);filter:brightness(80%)}.userCard .bottom{position:relative;padding-top:100px;text-align:center}.userCard .bottom img{position:absolute;top:-80px;left:70px;border-radius:50%;border:0.5rem solid white}.userCard .bottom h2{font-size:2rem}.userCard .bottom h3{font-size:1.5rem;color:grey}@media only screen and (max-width: 737px){#app>h1{font-size:3rem}}

